import logo from './logo.jpg';
import './App.css';
import Header from './components/Header';
import styled from 'styled-components/macro';
import { useDispatch, useSelector } from "react-redux";
import { setPosts } from "./redux/postsAction";
import { setPost } from "./redux/postAction";
import { useState, useEffect } from 'react';
import Post from './components/Post';

const WholeWrapper = styled.div`
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b0c0c0;
  color: white;
  font-size: 34px;
`;

const MainWrapper = styled.div`
  width: 100%;
  max-width: 1200px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 160px;
  /* border: 0.5px solid #ccccff; */
  
  /* @media only screen and (max-width: 768px) {
    width: 100%;
  } */
`

const ContentWrapper = styled.div`
  width: calc(100% - 100px);
  padding: 50px;
`
  
const markdownFiles = require.context('../public/writings', false, /\.md$/)
.keys()
.map(a => a.substring(1));

function App() {
  const dispatch = useDispatch();
  const posts = useSelector(state => state.postsReducer);

  useEffect(() => {
    // Promise.all(markdownFiles.map(file => 
    //   fetch("/writings" + file)
    //     .then((res) => {
    //       return {
    //        title: file,
    //        text: res.text()
    //     }})
    //   )
    // )
    //   .then((posts) => dispatch(setPosts(posts)))
    //   .catch((err) => console.error(err));
    console.log("markdownfiles",markdownFiles)

    dispatch(setPosts(markdownFiles.map(file => "/writings" + file)));
  }, [])

  useEffect(() => {
    if (posts.posts == undefined) return;

    console.log("posts",posts)
    fetch(posts.posts[0])
      .then(res => res.text())
      .then(text => {
        console.log("skfjflsk", text)
        dispatch(setPost({
        title: markdownFiles[0],
        text: text
      }))}
      )
  }, [posts])

  return (
    <WholeWrapper>
      <MainWrapper>
        <Header />
        <ContentWrapper>
          <Post />
        </ContentWrapper>
      </MainWrapper>
    </WholeWrapper>
  );
}

export default App;
